.plat-button{position:relative;min-width:60px;height:36px;font-size:14px;color:#333;background-color:#fff;border-width:1px;border-radius:4px;outline:none;border:1px solid transparent;padding:0 10px;overflow:hidden;cursor:pointer;-webkit-transition:all 1.3s ease;-o-transition:all 1.3s ease;transition:all 1.3s ease;-webkit-transition:background .5s ease;-webkit-box-shadow:0px 0px 2px 1px rgba(255,255,255,.2);box-shadow:0px 0px 2px 1px rgba(255,255,255,.2)}.plat-button:active,.plat-button:focus{outline:none}.plat-button-default{color:#333;border-color:#555}.plat-button-default:active,.plat-button-default:focus,.plat-button-default:hover{background-color:rgba(21,85,154,.3)}.plat-button-primary{color:#fff;background-color:#15559a}.plat-button-primary:active,.plat-button-primary:focus,.plat-button-primary:hover{background-color:#7191b3}.plat-button-primary:active,.plat-button-primary:focus{-webkit-box-shadow:0px 0px 2px 1px #7999bc;box-shadow:0px 0px 2px 1px #7999bc}.plat-button-info{color:#fff;background-color:#409eff}.plat-button-info:active,.plat-button-info:focus,.plat-button-info:hover{background-color:#86b5e6}.plat-button-success{color:#fff;background-color:#67c23a}.plat-button-success:active,.plat-button-success:focus,.plat-button-success:hover{background-color:#9ac783}.plat-button>.ripple-ball{position:absolute;display:block;width:10px;height:10px;border-radius:50%;background:rgba(0,0,0,.4);-webkit-animation:ripple 1s;animation:ripple 1s}@-webkit-keyframes ripple{0%{-webkit-transform:scale(2);transform:scale(2)}85%{-webkit-transform:scale(25);transform:scale(25)}100%{opacity:0}}@keyframes ripple{0%{-webkit-transform:scale(2);transform:scale(2)}85%{-webkit-transform:scale(25);transform:scale(25)}100%{opacity:0}}